.sidebar{position:fixed;top:0;height:100vh;background-color:#fff;z-index:10;width:280px;-webkit-transition:-webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);transition:-webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);transition:transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);transition:transform 0.3s cubic-bezier(0.55, 0, 0.1, 1), -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1)}.sidebar .avatar-container{padding:16px;background-color:#e8eaf6}.sidebar .avatar-container .avatar{width:48px;height:48px;border-radius:50%;background-color:rgba(0,0,0,0.54);margin-right:16px}.sidebar .avatar-container .user-info,.sidebar .avatar-container .avatar{display:inline-block;vertical-align:middle}.sidebar.closed{-webkit-transform:translateX(-280px);transform:translateX(-280px)}.sidebar.locked-open{position:relative;-webkit-transform:translateX(0px);transform:translateX(0px);top:-40px;height:100vh}@media only screen and (max-width: 768px){.sidebar.locked-open{-webkit-transform:translateX(0px);transform:translateX(0px);top:0}.sidebar.closed{-webkit-transform:translateX(-280px);transform:translateX(-280px)}}.app-bar.locked-open+.container{width:calc(100% - 280px);margin-left:280px}@media only screen and (max-width: 768px){.app-bar.locked-open+.container{padding-left:0;margin-left:auto;width:100vw}}.sidebar-backdrop{position:fixed;height:100vh;width:100vw;background-color:rgba(0,0,0,0.52);z-index:5;top:0}.sidebar-backdrop.show{-webkit-animation:fade-in .3s forwards;animation:fade-in .3s forwards}.sidebar-backdrop.hide{-webkit-animation:fade-out .3s forwards;animation:fade-out .3s forwards}@-webkit-keyframes fade-in{0%{display:block;opacity:0}100%{opacity:1}}@keyframes fade-in{0%{display:block;opacity:0}100%{opacity:1}}@-webkit-keyframes fade-out{0%{opacity:1}100%{opacity:0;visibility:hidden}}@keyframes fade-out{0%{opacity:1}100%{opacity:0;visibility:hidden}}



