.whiteframe-z1{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)}.whiteframe-z2,.material-select-option-container{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)}.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)}.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)}.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)}.material-select-container{position:relative}.material-select{-webkit-box-align:center;-ms-flex-align:center;align-items:center;display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;padding:16px;cursor:pointer;font-size:16px}.material-select-option-container{position:absolute;top:-4px;z-index:10;background-color:#fff;padding:8px 0;min-height:48px;max-height:256px;overflow:auto}.material-select-option{font-size:16px;height:48px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;padding:0 16px;cursor:pointer;-webkit-transition:opacity 0.25s cubic-bezier(0, 0, 0.2, 1);transition:opacity 0.25s cubic-bezier(0, 0, 0.2, 1)}.material-select-option:hover{background-color:#EEEEEE}.material-select-backdrop{position:fixed;height:100vh;width:100vw;top:0;left:0;z-index:5}



