.app-bar{position:fixed;width:100%;height:0;top:0;right:0;z-index:10}.app-bar .toolbar{z-index:10}.app-bar.locked-open .toolbar{margin-left:280px;width:calc(100% - 280px)}.app-bar.locked-open .app-bar-menu{display:none}@media only screen and (max-width: 768px){.app-bar.locked-open .toolbar{margin-left:0;width:100%}.app-bar.locked-open .app-bar-menu{display:block}}.app-bar .active{color:#3F51B5;font-weight:500}.list-item{cursor:pointer}.sidebar-nav .list{margin:0}.secondary-list .list-item{padding-left:32px}.app-bar-menu .icon-button-wrapper.flat.primary i{color:#fff !important}



