.checkbox-container{width:40px;height:40px;display:inline-block;position:relative;vertical-align:middle;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.checkbox-container i{position:absolute;top:0;margin:12px;font-size:16px;opacity:0;color:#fff;-webkit-transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1);transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1);font-weight:700}.checkbox-container .checkbox{margin:10px;width:20px;height:20px;border-width:2px;border-style:solid;border-radius:2px;border-color:rgba(0,0,0,0.54);vertical-align:middle;-webkit-transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1);transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1)}.checkbox-container.has-value i{opacity:1}.checkbox-container.has-value .checkbox{background-color:#3F51B5;border-color:#3F51B5}.checkbox-container.has-value.secondary-color .checkbox{background-color:#e91e63;border-color:#e91e63}



