.material-input-container{margin:16px 0;position:relative;width:100%}.material-input-container label{display:block;position:absolute;-webkit-transform:translateY(4px);transform:translateY(4px);-webkit-transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1);transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1);color:rgba(0,0,0,0.38);pointer-events:none}.material-input-container input,.material-input-container textarea{background-color:transparent;outline:none;box-sizing:border-box;border-width:0 0 1px;border-style:solid;border-color:rgba(0,0,0,0.12);padding:4px 0;font-size:16px;width:100%;max-width:100%;margin-bottom:16px}.material-input-container.focus label,.material-input-container.has-value label{-webkit-transform:translateY(-16px);transform:translateY(-16px);font-size:12px}.material-input-container.focus label{color:#3F51B5}.material-input-container.focus input,.material-input-container.focus textarea{border-bottom:2px solid #3F51B5;margin-bottom:15px}



