.whiteframe-z1,.card{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)}.whiteframe-z2{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)}.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)}.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)}.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)}.card{background-color:#fff;border-radius:2px}.card .primary-title{padding:24px 16px 16px 16px}.card .primary-title .title{font-size:24px;font-weight:400}.card .primary-title .subtitle{font-size:14px;color:rgba(0,0,0,0.54);font-weight:400}.card .actions{padding:8px}.card .card-supporting-text{font-size:14px;margin:16px}.card .card-actions{padding:8px}.card .card-actions .button{margin-bottom:0;color:rgba(0,0,0,0.87)}.card .card-content{padding:32px 16px 16px 16px;display:block}.card img{width:100%}



